import { useSession, signOut } from "next-auth/react";
import { useEffect } from "react";

const HomePage = () => {
  const { data: session, status } = useSession();

  useEffect(() => {
    if (status === "loading") return; // Chờ khi status đang loading

    if (session) {
      // Kiểm tra session đã hết hạn chưa (sử dụng thông tin từ token hoặc db)
      const currentTime = new Date();
      const expiresAt = new Date(session.expires); // Dùng expires thay vì expiresAt

      if (expiresAt <= currentTime) {
        // Nếu session đã hết hạn, yêu cầu đăng nhập lại và xóa session
        signOut(); // Đăng xuất người dùng
      }
    } else {
      // Nếu không có session, yêu cầu đăng nhập lại
      signOut(); // Đăng xuất người dùng
    }
  }, [session, status]);

  if (status === "loading") {
    return <div>Loading...</div>;
  }

  if (!session) {
    return <div>Please log in</div>; // Hiển thị thông báo yêu cầu đăng nhập
  }

  return (
    <div>
      <h1>Welcome, {session.user?.name}</h1>
      <p>Your session is valid until {session.expires}</p> {/* Hiển thị expires */}
    </div>
  );
};

export default HomePage;

